.container{
    margin: auto;
    max-width: 600px;
}

.done{
    text-decoration: line-through;
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.item{
    display:flex;
    justify-content:space-between;
    align-items:center;
    font-size: 30px;
    width: 100%;
    padding: 15px;
    margin-top: 15px;
    
}

.card{
    box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.2);
}

img{
    height:30px;
    width: 30px;
}

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

button{
    border: none;
    background-color:white
}

.addButton{
    width: 50px;
    height: 50px;
    font-size: 20px;
    border:none;
    background-color:skyblue;
    color:white;
    border-radius:25px;
}

.modal{
    width: 100vw;
    height: 100vh;
    background-color:rgba(0, 0, 0, 0.2);
    position:fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content:center;
    align-items:center;
}

.cardModal{
    padding:20px;
    background-color: white;
}
.formButton{
    border:1px solid black;
    height:21px;
    border-radius:3px;
    margin-left: 5px;
}
.hideModal{
    display:none;
}